import topheader from '@/components/chat/topheader'
import _ from "underscore";
import f from "@/application/functions";
import {
  VsaList,
  VsaItem,
  VsaHeading,
  VsaContent,
  VsaIcon
} from 'vue-simple-accordion';
import {Swiper, SwiperSlide} from "vue-awesome-swiper";

import contacts from '@/components/contacts'
import {PhotoSwipe, PhotoSwipeGallery} from 'v-photoswipe'
import userspic from '@/components/assets/userspic'
import {mapState} from "vuex";

export default {
  data: () => {
    return {
      swiperOption: {
        watchSlidesProgress: true,
        watchSlidesVisibility: true,
        slidesPerView: 5,
        direction: 'horizontal',
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      encryptedEvents: [],
      imageEvents: [],
      fileEvents: [],
      menuItems: [
        {
          click: "adminMakeModer",
          title: "Make Moderator",
          icon: "fas fa-user-shield",

        },

        {
          click: "adminBan",
          title: "Ban",
          icon: "fas fa-ban",

          upload: {
            multiple: true
          },
        },
        {
          click: "adminKick",
          title: "Kick",
          icon: "fas fa-user-times"
        }
      ],
      menuItemsModer: [
        {
          click: "moderBan",
          title: "Ban",
          icon: "fas fa-ban"
        },
        {
          click: "moderKick",
          title: "Kick",
          icon: "fas fa-user-times"
        }
      ],
      admin: String,
      imagesList: [],
      localRoomName: '',
      humanSize: Number,
      inviteUserOpened: false,
      inviteUsers: 'inviteUsers',
      msList: [],
      isOpen: false,
      actionMemberShow: false,
      options: {
        index: 0,
        arrowEl: false,
        fullscreenEl: false,
        shareEl: false
      },
      roomName: '',
      nameEdit: false,
      accordionList: [
        {title: 'Members'},
        {title: 'Media'},
        {title: 'Files'}
      ],
      isActive: null,
      isActiveAction: null,
      currentUser: '',
      actionUser: {},
      actionUserMenu: false,
    }

  },
  props: {
    chat: {},
    events: {}
  },
  components: {
    topheader,
    VsaList,
    userspic,
    VsaItem,
    Swiper,
    SwiperSlide,
    VsaHeading,
    VsaContent,
    VsaIcon,
    contacts,
    'v-photoswipe': PhotoSwipe,
    'v-photoswipe-gallery': PhotoSwipeGallery
  },
  computed: {
    ...mapState([
      'minimized',
      'active'
    ]),
    users: function () {

      var users = _.filter(_.map(this.chat?.getJoinedMembers() || [], (m) => {

        return this.$f.deep(this, '$store.state.users.' + m.name) || null

      }), (m) => {
        return m && m.id != this.core.user.userinfo?.id
      })


      console.log('users', users)

      return users

    },
    m_chat: function () {

      if (this.chat.roomId) {

        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)


        if (m_chat) {
          //console.log(m_chat, m_chat.getAvatarUrl(), m_chat.currentState.getStateEvents('m.room.member'))
        }

        //console.log(this.core.mtrx.client.getRoom(this.chat.roomId))
        return m_chat || {}
      }

    },
    galleryImages: function () {
      let imgArr = []
      this.imageEvents.forEach(function (event) {
        if (event._clearEvent.content.msgtype === 'm.image') {
          imgArr.push({
            src: event._clearEvent.content.url,
            w: event._clearEvent.content.info.w,
            h: event._clearEvent.content.info.h
          })
        }
      })
      return imgArr
    },
    isRole: function () {
      let userID = this.core.mtrx.client.getUserId()
      let role = ''
      _.mapObject(this.chat.currentState.members, function (val, key) {
        if (userID === key && val.powerLevel === 100) {
          console.log(val.powerLevel, "owner", val.name)
          role = 'owner'
        }
        if (userID === key && val.powerLevel === 50) {
          console.log(val.powerLevel, "moderator", val.name)
          role = 'moderator'
        }
        if (userID === key && val.powerLevel === 0) {
          console.log(val.powerLevel, "member", val.name)
          role = 'member'
        }

      })
      this.currentUser = userID
      return role
    },
    membersList: function () {
      let members = []
      let pNetUsers = this.$store.state.users
      _.mapObject(this.chat.currentState.members, function (val, key) {
        _.map(pNetUsers, function (user) {
          if ('@' + user.id + ':matrix.pocketnet.app' === val.userId) {
            return members.push({id: val.userId, image:user.image, name: user.name, level: val.powerLevel, roomStatus: val.membership})
          }
        })

      })
      return members
      // console.log(members, "ARRAYS")
    },
    getMembersList: function () {
      let pNetUsers = this.$store.state.users
      let members = []
      let users = []
      let joinedMembers = []
      let invitedMembers = []
      let creatorId = ''
      let admins = []
      _.mapObject(this.chat.currentState.members, function (val, key) {
        if (val.powerLevel === 100) {
          creatorId = f.getmatrixid(key)
        }
        if (val.powerLevel === 50) {
          let userID = f.getmatrixid(key)
          admins.push(userID)
        }
        if (val.membership === 'join') {
          joinedMembers.push({id: f.getmatrixid(key)})
        }
        if (val.membership === 'invite') {
          invitedMembers.push({id: f.getmatrixid(key)})
        }
        members.push({id: f.getmatrixid(key)})
        console.log(val.membership)
      })
      _.map(pNetUsers, function (id) {
        users.push(id)
      })
      console.log(admins, "ADMIN ADMIN ADMINS")
      invitedMembers = f.filterByUserId(invitedMembers, users)
      joinedMembers = f.filterByUserId(joinedMembers, users)
      console.log(joinedMembers, invitedMembers)
      joinedMembers.filter(function (id) {
        if (id.id === creatorId) {
          id.role = "Owner"
        }
        admins.filter(function (adminId) {
          if (id.id === adminId)
            id.role = "Moderator"
        })
      })
      return joinedMembers
    },
    changeRoomName: function () {
      let roomName = this.chat.name
      return roomName.replace('_', ' ')
    }

  },
  mounted() {
    // this.changeName()
    this.eventsList()
  },
  methods: {
    eventsList() {

      if (typeof this.events.filter == 'function') {

        this.imageEvents = this.events.filter(event => event.event.type === 'm.room.encrypted' && event._clearEvent.content.msgtype === 'm.image')

        this.fileEvents = this.events.filter(event => event.event.type === 'm.room.encrypted' && event._clearEvent.content.msgtype === 'm.file')

      }
    },
    jsonConvertor(data) {
      console.log(data, "Datatattt")
      return JSON.parse(data)
    },
    toggleItem(index) {
      console.log("cliecked item")
      this.isActive = this.isActive === index ? null : index;
      this.actionMemberShow = null
    },

    menuItemClickHandler(item, rowObject) {
      console.log(rowObject, "item")
      if (item.click === 'adminMakeModer') {
        console.log("moderatorMaking", this.$refs.dropdownMenu)
        this.makeAdmin(rowObject.user)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }
      if (item.click === 'adminBan') {
        console.log("banMaking", this.$refs.dropdownMenu)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }
      if (item.click === 'adminKick') {
        console.log("makeKick", this.$refs.dropdownMenu)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }
      if (item.click === 'moderBan') {
        console.log("makeKick", this.$refs.dropdownMenu)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }
      if (item.click === 'moderKick') {
        console.log("makeKick", this.$refs.dropdownMenu)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }
      // console.log(item.click, "clicked item is here")
      this.actionUser = rowObject
    },
    forgetRoom() {
      this.core.mtrx.client.kick(this.chat.roomId, this.currentUser, 'leave').then(result => {
        this.core.mtrx.client.forget(this.chat.roomId, true)
      }).then(r => {
        this.$emit('closeAboutRoom', false)
      })
      this.$router.push({path: '/chats'})

    },
    changeName() {
      this.localRoomName = this.chat.name
      this.localRoomName.replace('_', ' ')
    },
    saveRoomName() {
      this.nameEdit = false
      // this.core.mtrx.client.setRoomName(this.chat.roomId)
      this.roomName.replace(" ", "_")

      this.core.mtrx.client.setRoomName(this.chat.roomId, this.roomName)
      // console.log(this.chat.roomId, "changeRoomName")
    },
    makeAdmin(user) {
      let event = this.chat.currentState.getStateEvents("m.room.power_levels")
      // console.log(this.chat.currentState.getStateEvents("m.room.power_levels"), "eveht eveht")
      this.core.mtrx.client.setPowerLevel(this.chat.roomId, userID, 50, event[0]).then(r => {
        console.log(r, "50 505 05", user.id)
      })

    },
    showEditRoomName() {
      this.nameEdit = true
      this.roomName = this.chat.name
      console.log(this.chat.name, "showEditRoomName")

    },
    editRoomName() {
      console.log(this.roomName, "omg its name changed")
    },

    memberAction(user) {
      console.log(user, "user user user")
      this.actionMemberShow = !this.actionMemberShow
      this.actionUser = user
      this.isActiveAction = this.isActiveAction === user.index ? null : user.index;

      console.log('clicked')
    },
    kickUser() {
      console.log(this.actionUser, "ussssseeeeer")
      let userId = '@' + this.actionUser.user.id + ':matrix.pocketnet.app'
      console.log(userId)
      this.core.mtrx.client.kick(this.chat.roomId, userId).then(this.$nextTick(function () {
        console.log(this.getMembersList, "get memebers list")
      }))
      console.log(this.core.mtrx.client.getJoinedRoomMembers(), "this chat after user kicked")

      // this.getMembersList.splice(this.actionUser.index, 1)
      // console.log(this.getMembersList)
      // this.$set(this.getMembersList, )
    },
    showPhotoSwipe(index) {
      console.log(index, "index index index")
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },
    accordion: function (event) {

      //Раскомментировать, если нужно, чтобы закрывались остальные пункты при открытии текущего
      // 	var matches = document.querySelectorAll('.accordion-item-head');
      //  	for(var i = 0; i < matches.length; i++) {
      //  		matches[i].classList.remove('active');
      //	 }

      event.target.classList.toggle('active');
    },
    hidePhotoSwipe() {
      this.isOpen = false
    },
    showUserAbout(user) {
      this.$emit('showAbout', user)
    },
    getClick(room) {
      this.$emit('showRoomInfo', room)
    },
    getMembersCount(chat) {
      return chat.getJoinedMemberCount()
    },
    humanReadableSize(data) {
      return f.formatBytes(data)
    },
    getJoinedMembers() {
      let pNetUsers = this.$store.state.users
      let users = []
      let membersId = []
      let jMembers = []
      let adminId = ''
      let moderatorId = ''
      return this.core.mtrx.client.getJoinedRoomMembers(this.chat.roomId).then(members => {

        _.map(pNetUsers, function (id) {
          users.push(id)
        })
        console.log(users, "users")
        _.map(members.joined, function (id) {
          membersId.push({id: id.display_name})
        })
        console.log(membersId, "mIds")


        jMembers = f.filterByUserId(membersId, users)
        _.mapObject(this.chat.currentState.members, function (val, key) {
          if (val.powerLevel === 100) {
            adminId = f.getmatrixid(key)
          }
          if (val.powerLevel === 50) {
            moderatorId = f.getmatrixid(key)
          }
        })
        console.log(jMembers, "jMembers")

        _.map(jMembers, function (member) {
          if (member.id === adminId) {
            member.role = 'owner'
          }
          if (member.id === moderatorId) {
            member.role = 'moderator'
          }
        })
        return jMembers
      }).then(e => {
        return e
      })


    },

    modalInviteUser() {
      this.inviteUserOpened = true
    },
    closeModal() {
      this.inviteUserOpened = false
    },
    closeContactModal(value) {
      console.log(value, " this value modal contacts")
      this.inviteUserOpened = value
    }
  }
}