import {mapState} from 'vuex';
import list from '@/components/contacts/list/index.vue'
import _ from "underscore";
import preview from '@/components/contacts/preview/index.vue'
import contact from '@/components/contact/index.vue'

import f from '@/application/functions.js'

export default {
  name: 'contacts',
  props: {
    source : String,
    mode: {
      type: String,
      default: function () {
        return ''
      }
    },
    title: String,
    chatRoomId: String
  },

  components: {
    list,
    contact
  },

  data: function () {

    return {
      aboutShow: false,
      loading: false,
      fromSearch: [],
      myContacts: Object,
      inputText: '',
      groupContacts: [],
      selected: false,
      groupName: '',
      contactsPage: 'Contacts',
      showGroupsActions: false,
      contact: {}
    }

  },

  created: () => {

  },
  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    ...mapState([
      'contactsMap',
      'pocketnet',
      'minimized',
      'active'
    ]),

    u: function () {
      console.log(this.$route.query.u, "UUUUUUUUU ITS U")
      return this.$route.query.u
    },
    
    contactsList: function () {
      let client = this.core.mtrx.client

     _.mapObject(this.contactsMap, function (key, value) {

        if(client.getUser(`@${value}:matrix.pocketnet.app`) !== null){
          key.isUser = true
        }else{
          key.isUser = false
        }
      })

      return this.contactsMap

    },
    direct: function () {
      if (this.groupContacts.length === 1) {
        return true
      }
    },
    tetatetid: function () {
      return this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)
    },
    gName: function (){
      let name  = this.groupName
      return name.replace(/ /g, '_')
    },
  }),
  methods: {
    chat: function () {

      this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)

    },
    search: function (text) {
      console.log(this.mode)
      this.inputText = text
      this.core.user.searchContacts(text).then(users => {
        if (this.groupContacts.length === 0) {
          // console.log(users, "users Users from search")

          this.fromSearch = users
          this.myContacts = _.where(this.contactsMap, {name: text})
        } else {
          this.myContacts = _.where(this.contactsMap, {name: text})
          this.fromSearch = users
          let groupContacts = this.groupContacts
          this.fromSearch.forEach(function (user) {
            // console.log(user, "from serach first itearation")
            groupContacts.forEach(function (gUser) {
              if (gUser.id === user.id) {
                user.selected = true
              }
            })
          })
        }
      })
    },
    selectedContact(contact) {
      this.contact = contact
      if (contact.selected !== true) {
        this.groupContacts.push(contact)
        var c = this.fromSearch.filter(n => this.groupContacts.indexOf(n) !== -1);
        this.fromSearch.forEach(function (user) {
          c.forEach(function (gUser) {
            if (gUser.id === user.id) {
              user.selected = true
            }
          })
        })
      } else {
        var uncheck = this.groupContacts.indexOf(contact)
        this.fromSearch.forEach(function (user) {
          if (user.id === contact.id) {
            user.selected = false
          }
        })
        this.groupContacts.splice(uncheck, 1)
      }
    },
    removeEl(index) {
      let groupContacts = this.groupContacts
      this.fromSearch.forEach(function (user) {
        groupContacts.forEach(function (gUser) {
          if (gUser.id === user.id) {
            user.selected = false
          }
        })
      })
      this.groupContacts.splice(index, 1)
    },

    createGroup(users) {
      const data = this.core.mtrx.kit.groupId(users)
      if (this.groupName.length !== 0) {
        console.log('create room 1')
        return this.core.mtrx.client.createRoom({
          room_alias_name: '#' + data.hash,
          visibility: 'private',
          invite: data.idForInviting,
          name: '#' + this.groupName,
          initial_state: [
            {
              "type": "m.room.guest_access",
              "state_key": "",
              "content": {
                  "guest_access": "can_join"
              }
            },
            {
              "type": "m.room.encryption",
              "state_key": "",
              "content": {
                  "algorithm": "m.megolm.v1.aes-sha2"
              }
            }
          ]
        }).then(chat => {

          this.$router.push({
            path: 'chat',
            query: {'id': chat.room_id}
          })
        })

      } else {
        console.log('create room 2')
        return this.core.mtrx.client.createRoom({
          room_alias_name: '#' + data.hash,
          visibility: 'private',
          invite: data.idForInviting,
          name: '#' + "New Room",
          initial_state: [
            {
              "type": "m.room.guest_access",
              "state_key": "",
              "content": {
                  "guest_access": "can_join"
              }
            },
            {
              "type": "m.room.encryption",
              "state_key": "",
              "content": {
                  "algorithm": "m.megolm.v1.aes-sha2"
              }
            }
          ]
        }).then(chat => {

          this.$router.push({
            path: 'chat',
            query: {'id': chat.room_id}
          })
          // http://localhost:8080/chat?id=%21ehBaimKfwFIonXIoJT%3Amatrix.pocketnet.app
        })
      }
    },
    inviteUserAction(users) {
      let client = this.core.mtrx.client
      let roomID = this.chatRoomId
      users.forEach(function (user) {
        let matrixID = '@' + `${user.id}` + ':matrix.pocketnet.app'
        client.invite(roomID, matrixID).then(r => {
        })
      })
      this.$emit('closeModal', false)
    }
  },
}